import * as React from "react";
import "./echo.css"

import { EchoServicePromiseClient } from "../../generated/echo_grpc_web_pb";
import { EchoRequest } from "../../generated/echo_pb";

export interface EchoProps { client: EchoServicePromiseClient; }

export function Echo(props: EchoProps) {
    const inputRef = React.useRef(null);
    const logRef = React.useRef(null);
    const [inputValue, setInputValue] = React.useState("");

    React.useEffect(() => {
        logRef.current.scrollTop = logRef.current.scrollHeight;
    }, [inputValue, logRef])

    const handleSend = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const input = inputRef.current.value;

        if (input !== "") {
            const request = new EchoRequest();
            request.setContents(input);

            props.client.echo(request).then(value => {
                setInputValue(inputValue + value.getContents() + "\n");
            });

            inputRef.current.value = "";
        }
    };

    return (
        <div className="echo">
            <textarea className="echoLog" disabled value={inputValue} ref={logRef}></textarea>
            <form className="echoBar" onSubmit={handleSend}>
                <input className="echoInput" type="text" ref={inputRef} />
                <input className="echoButton btn btn-primary" type="submit" value="Echo" />
            </form>
        </div>
    )
}